.input_form {
  width: 300px;
  margin-right: 20px;
  color: var(--color);
  margin-left: 10px;
}

.input_corpus {
  width: 50px;
  margin-right: 20px;
  color: var(--color);
  margin-left: 10px;
}

.input_form,
.input_corpus {
  background: transparent;
  border: 3px solid rgb(255, 255, 255);
  outline-width: 0;
  font-size: 25px;
  height: 60px;
  font-family: var(--font-family_light), monospace;
}

input:focus {
  outline: 0 none;
  border: 3px solid rgb(255, 255, 255);
}

label {
  font-size: 28px;
  color: var(--color);
  font-family: var(--font-family_bold), monospace;
  margin-right: 30px;
  text-align: center;
}

.button_znach {
  border: none;
  background-color: var(--btn-background-color);
  cursor: pointer;
  width: 320px;
  height: 120px;
  color: var(--color);
  box-shadow: 0px 0px 55px 0px rgba(99, 99, 99, 0.54) inset;
  -webkit-box-shadow: 0px 0px 55px 0px rgba(99, 99, 99, 0.54) inset;
  -moz-box-shadow: 0px 0px 55px 0px rgba(99, 99, 99, 0.54) inset;
}

.button_znach.disabled {
  opacity: 0.5;
}

.select_form {
  width: 300px;
  background: transparent;
  border: 3px solid rgb(255, 255, 255);
  outline-width: 0;
  font-size: 25px;
  height: 75px;
  color: var(--color);
  font-family: var(--font-family_light), monospace;
}

.button_count {
  width: 80px;
  height: 65px;
}

option {
  font-size: 15px;
  background: rgb(255, 255, 255);
}
