.slick-slider {
  width: 95%;
  height: 1200px;
  font-family: var(--font-family_light);
}

.slick-list {
  width: 106%;
}

.slick-dots {
  font-size: 0;
}

.slick-dots li button:before {
  font-size: 20px;
}

.report_block {
  counter-reset: my-counter;

}

.report_block li {
  list-style-type: none;
  position: relative;
  padding-left: 50px;
}

.report_block li::before {
  content: counter(my-counter) '.';
  counter-increment: my-counter;
  position: absolute;
  left: 0;
}

.report_block {
  width: 100%;
}

.report_text {
  font-size: 50px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.report_text.border {
  border: 2px solid green;
}

.report_text_bold {
  font-size: 50px;
  font-family: var(--font-family_bold);
}

.instr_img {
  width: 695px;
  height: 1000px;
  margin-right: 10px;
  margin-bottom: 10px;
}

.report_block_two .ol_two {
  counter-reset: item 3;
  list-style: none;
  padding: 0;
}

.li_slider {
  counter-increment: item;
}

.li_slider::before {
  content: counter(item) '. ';
}

.li_slider:nth-child(1)::before {
  content: counter(item, 4) ' ';
}

.report_usb {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
}

.button_usb {
  width: 330px;
  height: 120px;
  outline: none;
  font-size: 35px;
  font-family: var(--font-family_bold);
  border: none;
  background: #21eb06;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.button_usb.change_day {
  width: 100%;
}

.button_usb.disabled {
  background: #8f8f8f;
  opacity: 0.5;
}

.button_block {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.button_block_one {
  display: flex;
  flex-direction: row;
  gap: 30px;
}

.usb_img {
  width: 600px;
  height: 600px;
}

.toast-position {
  top: 350px !important;
  width: 500px !important;
  font-size: 30px;
}

@media screen and (min-width: 1900px) {
  .toast-position {
    top: 350px !important;
    width: 1000px !important;
    font-size: 80px;
  }
}


