:root {
  --background-color: #e2e2e2;
  --header-background: #e0e0e0;

  /* fonts */
  --font-family_light: 'agro';
  --font-family_bold: 'agro_bold';

  /* text color  */
  --color: black;

  /* buttons */
  --btn-background-color:  #ffffff;
  --btn-start-page: #50ac41;
  --text-color-btn: #36395a;

  /* buttons nav */
  --btn-page-nav: #a1d698;
  --btn-page-push: #2eb135;
  --filer-icon: 0;

  --border-sort-page: #8d8d8d;
  --line-sort-page: #000;

  /* background color cards setting page */
  --card-background-color: #2eb135;

  /* background-header; */
  --background-header: #2eb135;

  /* background image logo */
  --filter: none;

  /* background color button minus */
  --btn-minus: #b61b1b;
}

[data-theme='dark'] {
  --background-color: #868788;
  --header-background: #8d8d8d;

  /* text color  */
  --color: #fafafa;

  --btn-start-page: #2b5a24;
  --text-color-btn: #fafafa;

  --btn-background-color:  #333333;

  /* buttons nav */
  --btn-page-nav: #59a04f;
  --btn-page-push: #407039;
  --filer-icon: 1;

  --border-sort-page: #ffffff;
  --line-sort-page: #ffff;

  /* background color cards setting page */
  --card-background-color: #3be641;

  /* background image logo */
  --filter: brightness(0.5);
}

[data-lang='eng'] {
  --padding: 0.1px;
}

* {
  transition: background-color 0.3s ease-in-out;
}
