.button_control_over {
    width: 300px;
    height: 100px;
    border: none;
    font-size: 40px;
    font-family: var(--font-family_bold);
    background-color: #1bb630;
    color: var(--color);
    border-radius: 10px;
}

@media screen and (min-width: 1920px) {
    .button_control_over {
        width: 600px;
        height: 200px;
        border: none;
        font-size: 80px;
        font-family: var(--font-family_bold);
        background-color: #1bb630;
        color: var(--color);
        border-radius: 10px;
    }
}