.btn_weight,
.btn_change_weight {
    border: none;
    outline: none;
    font-size: 35px;
    font-family: var(--font-family_bold);
    border-radius: 10px;
}

.btn_weight {
    width: 120px;
    height: 120px;
}

.btn_change_weight {
    width: 300px;
    height: 80px;
}

.btn_weight.active {
    background-color: green;
}

.imagePoint {
    width: 680px;
    height: 605px;
}

@media screen and (min-width: 1900px) {
    .btn_weight {
        width: 200px;
        height: 200px;
    }

    .btn_change_weight {
        width: 600px;
        height: 180px;
        font-size: 70px;
    }

    .imagePoint {
        width: 1310px;
        height: 1000px;
    }
}