.btn_weight_section {
    width: 120px;
    height: 120px;
    font-size: 35px;
    font-family: var(--font-family_bold);
    border-radius: 10px;
}

.btn_weight_section.active {
    background-color: green;
}


@media screen and (min-width: 1900px) {
    .btn_weight_section {
        font-size: 75px;
        width: 250px;
        height: 250px;
    }
}