.select_points {
  width: 150px;
  height: 80px;
  font-size: 20px;
  font-family: var(--font-family_bold);
}

.btn_arrow {
  width: 120px;
  height: 100px;
  border-radius: 10px;
  outline: none;
  border: none;
}

.btn_arrow.active {
  background-color: green;
}

.select_change_weight,
.input_change_weight {
  width: 300px;
  height: 50px;
  font-size: 25px;
  font-family: var(--font-family_light);
}

.warning {
  color: red;
  font-size: 30px;
}

.not_warning {
  display: none;
}

.btn_weight,
.btn_change_weight {
  border: none;
  outline: none;
  font-size: 35px;
  font-family: var(--font-family_bold);
  border-radius: 10px;
}

.btn_weight {
  width: 120px;
  height: 120px;
}

.btn_weight.plus {
  background-color: var(--background-header);
}

.btn_weight.minus {
  background-color: var(--btn-minus);
}

.btn_change_weight {
  width: 300px;
  height: 80px;
}

.btn_weight.active {
  background-color: green;
}

.imagePoint {
  width: 680px;
  height: 605px;
}

@media screen and (min-width: 1900px) {
  .btn_weight {
    width: 150px;
    height: 150px;
  }

  .btn_change_weight {
    width: 400px;
    height: 100px;
    font-size: 55px;
  }

  .imagePoint {
    width: 900px;
    height: 670px;
  }

  .btn_weight.plus,
  .btn_weight.minus {
    font-size: 55px;
    width: 180px;
    height: 180px;
  }
}