.btn_nav,
.btn_nav_push {
  width: 150px;
  height: 120px;
  border-radius: 20px;
}

.btn_nav {
  position: relative;
  background: var(--btn-background-color);
  border-radius: 15px;
  border: none;
  box-shadow: 0px 0px 55px 0px rgba(99, 99, 99, 0.54) inset;
  -webkit-box-shadow: 0px 0px 55px 0px rgba(99, 99, 99, 0.54) inset;
  -moz-box-shadow: 0px 0px 55px 0px rgba(99, 99, 99, 0.54) inset;
}

.btn_nav:disabled,
.btn_nav[disabled] {
  border: 3px solid #999999;
  background-color: #cccccc;
  color: #666666;
  opacity: 0.5;
}

.icon_nav {
  width: 70px;
  height: 70px;
  filter: invert(var(--filer-icon));
}

.btn_nav_push {
  background: var(--btn-page-push);
  border: none;
  width: 150px;
  height: 120px;
  box-shadow: 0px 5px 10px 2px rgba(0, 0, 0, 0.2) inset;
}

.btn_nav::before {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  padding-left: 2px;
  padding-right: 2px;
  padding-bottom: 4px;
  left: -2px;
  top: 5px;
  z-index: -1;
  border-radius: 6px;
}

.btn_nav_push::before {
  top: -2px;
}

@media screen and (min-width: 1330px) {
  .btn_nav, .btn_nav_push {
    width: 250px;
    height: 200px;
  }
  .icon_nav {
    width: 100px;
    height: 100px;
  }
}
