.conveyor_icon {
    filter: invert(var(--filer-icon));
}

.tableTh {
    height: 110px;
}

.tableTr {
    font-size: 35px;
    font-family: var(--font-family_bold);
    height: 110px;
}

.tableTdFirst {
    width: 200px;
}

.imgAbout {
    width: 50px;
    height: 50px;
    filter: invert(var(--filer-icon));
}

@media screen and (min-width: 1920px) {
    .imgAbout {
        width: 90px;
        height: 90px;
    }

    .tableTh {
        height: 150px;
    }

    .tableTr {
        font-size: 60px;
    }

    .tableTdFirst {
        width: 375px;
    }
}