.password_text {
    font-size: 50px;
    font-family: var(--font-family_bold);
    margin-top: 10px;
    text-align: center;
    margin-bottom: 20px;
}

.btn_keyboard {
    border: none;
    outline: none;
    font-size: 35px;
    font-family: var(--font-family_bold);
    border-radius: 10px;
    width: 120px;
    height: 120px;
}

@media screen and (min-width: 1900px) {

    .password_text {
        font-size: 100px;
        font-family: var(--font-family_bold);
        margin-top: 10px;
        text-align: center;
        margin-bottom: 20px;
    }

    .password_text.stars {
        font-size: 150px;
    }

    .btn_keyboard {
        font-size: 85px;
        width: 280px;
        height: 280px;
    }

}