@import './globalStyle.css';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
}

a {
  text-decoration: none;
  color: inherit;
}

a:focus,
a:hover,
a:visited,
a:link,
a:active {
  text-decoration: none;
  color: inherit;
}

body {
  margin: 0;
  background: var(--background-color);

  /* запретить выделение текста */
  -webkit-user-select: none;
  /* Safari/Chrome */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* IE/Edge */
  user-select: none;
}

.header {
  background: var(--header-background);
  height: 415px;
}

@media screen and (min-width: 1536px) {
  .header {
    height: 470px;
  }
}

.logo {
  width: 100px;
  margin-right: 20px;
}

@font-face {
  font-family: 'agro';
  src: url(./assets/fonts/DIN2014-Light.ttf);
}

@font-face {
  font-family: 'agro_bold';
  src: url(./assets/fonts/DIN2014-ExtraBold.ttf);
}

.languagesBtn {
  width: 70px;
  height: 35px;
  margin-left: 10px;
  outline: none;
  border: 3px solid rgb(16, 207, 16);
  font-size: 25px;
  opacity: 1;
}

.languagesBtn.active {
  border: 3px solid rgb(207, 16, 16);
  opacity: 0.5;
}