.button_settings {
  border: none;
  background-color:var(--btn-background-color);
  cursor: pointer;
  width: 350px;
  height: 150px;
  font-size: 45px;
  color: var(--color);
  border-radius: 30px;
  font-family: var(--font-family_bold), monospace;
  box-shadow: 0px 0px 55px 0px rgba(99, 99, 99, 0.54) inset;
  -webkit-box-shadow: 0px 0px 55px 0px rgba(99, 99, 99, 0.54) inset;
  -moz-box-shadow: 0px 0px 55px 0px rgba(99, 99, 99, 0.54) inset;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.button_settings.load {
  opacity: 0.5;
}

p {
  font-size: 30px;
  color: var(--color);
  font-family: 'agro', monospace;
}

.calibr {
  font-size: 35px;
  font-weight: 500;
  color: var(--color);
  font-family: var(--font-family_bold), monospace;
}

@media screen and (min-width: 1920px) {
  .button_settings {
    width: 700px;
    height: 240px;
    font-size: 80px;
  }
  
}
