.button_control {
    color: #000000;
    font-size: 18px;
    width: 500px;
    height: 150px;
    border: none;
    font-size: 45px;
    font-family: var(--font-family_bold);
    background-color: #b61b1b;
    color: var(--color);
    border-radius: 10px;

}
.button_control_over {
    width: 300px;
    height: 100px;
    border: none;
    font-size: 40px;
    font-family: var(--font-family_bold);
    background-color: #b61b1b;
    color: var(--color);
    border-radius: 10px;
}

@media screen and (min-width: 1900px) {
    .button_control {
        font-size: 80px;
        width: 800px;
        height: 200px;
    }

    .button_control_over {
        width: 600px;
        height: 200px;
        border: none;
        font-size: 80px;
        font-family: var(--font-family_bold);
        background-color: #b61b1b;
        color: var(--color);
        border-radius: 10px;
    }
}