.calendar {
    position: absolute;
    transform: scale(2);
}

.calendar_closed {
    position: absolute;
    opacity: 0;
}

.calendar_button {
    width: 100%;
    height: 120px;
    outline: none;
    font-size: 35px;
    font-family: var(--font-family_bold);
    border: none;
    background: #21eb06;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

@media screen and (min-width: 1920px) {
    .calendar {
        transform: scale(3);
    }

    .calendar_button {
        width: 825px;
    }
}