 .btn_weight_sort {
     outline: none;
     border: none;
     font-size: 35px;
     width: 120px;
     height: 120px;
     font-family: var(--font-family_bold);
 }

 .btn_weight_sort.plus {
     background-color: var(--background-header);
 }

 .btn_weight_sort.minus {
     background-color: var(--btn-minus);
 }


 @media screen and (min-width: 1900px) {

     .btn_weight_sort.plus,
     .btn_weight_sort.minus {
         font-size: 70px;
         width: 265px;
         height: 265px;
     }
 }