.btn {
    width: 300px;
    height: 100px;
    border-radius: 10px;
    outline: none;
    border: 1px solid var(--background-header);
    font-size: 40px;
    font-family: var(--font-family_bold);
}

.active {
    background-color: var(--background-header);
}

.head {
    height: 110px;
    font-size: 45px;
    font-family: var(--font-family_bold);
}

.body {
    height: 110px;
    font-size: 45px;
    font-family: var(--font-family_light);
}

@media screen and (min-width: 1920px) {
    .btn {
        width: 500px;
        height: 150px;
        font-size: 60px;
    }
    .head {
        height: 180px;
        font-size: 60px;
        width: 200px;
    }
    .body {
        height: 180px;
        font-size: 60px;
    }
}