.button-30 {
  background: var(--btn-background-color);
  border-radius: 4px;
  border-width: 0;
  font-family: var(--font-family_bold);
  box-shadow: 0px 0px 75px 0px rgba(70, 70, 70, 0.54) inset;
  -webkit-box-shadow: 0px 0px 75px 0px rgba(99, 99, 99, 0.54) inset;
  -moz-box-shadow: 0px 0px 75px 0px rgba(99, 99, 99, 0.54) inset;
  font-size: 65px;
  width: 400px;
  height: 100px;
  border-radius: 30px;
  border: none;
  color: var(--color);
}

.button-30:disabled,
.button-30[disabled] {
  border: 3px solid #999999;
  background-color: #cccccc;

  opacity: 0.7;
}

.logo_bit {
  width: 940px;
  height: 650px;
}

@media screen and (min-width: 1536px) {
  .logo_bit {
    width: 1500px;
    height: 1000px
  }
}